
import { useToast } from "@/functions";
import { ref } from "vue";
import { useStore } from "vuex";

export default {
  name: "Register",
  setup() {
    const store = useStore();
    const toast = useToast();

    const email = ref("");
    const password = ref("");

    const registerUser = () => {
      store
        .dispatch("register", {
          email: email.value,
          password: password.value
        })
        .then(() => toast("Welcome to dogstagram", { type: "success" }))
        .catch(err => {
          toast(err.message, { type: "error" });
        });
    };

    return {
      registerUser,
      email,
      password
    };
  }
};
